          artist: {
            name
          }
        }
      ],
      carouselData: [],
      isLoad: true
    }
  },
  mounted () {
    this.aaa()
  },
  methods: {
    aaa () {
      const vm = this
      const head = { headers: { Authorization: 'Bearer m2CYdp5FHaDLHWhYoaHSIg==' } }
      // 取得情境電台列表
      this.axios.get('https://api.kkbox.com/v1.1/new-release-categories?territory=TW', head)
        .then(function (res) {
          let arr = []
          res.data.data.forEach(item => {
            vm.axios.get(`https://api.kkbox.com//v1.1/new-release-categories/${item.id}?territory=TW`, head)
              .then(function (res) {
                if (res.data.title === '綜合') {
                  vm.advertisingArea = res.data.albums.data
                }
                vm.albumList.push(res.data)
                let song = []
                res.data.albums.data.forEach(ele => {
                  song.push(ele)
                  if (song.length === 6) {
                    arr.push(song)
                    song = []
                  }
                })

                vm.carouselData.push(arr)
                arr = []
              })
              .catch(error => console.error(error))
          })
          vm.isLoad = false
        })
        .catch(error => console.error(error))
    }
  }
}
</script>

